<script>
import { useBornExtraFormInit, useBornFieldOptions } from "@/composable/born";
import { inject, onMounted, onUnmounted, ref, toRefs, watch } from "vue-demi";
import { emitter } from "@/event-bus";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            form,
            v$,
            save,
            loading,
            getBornExtra,
            medicationForCOS,
            medicationForCOSContainFSHOrLhOrFshPlusLH
        } = useBornExtraFormInit();

        const {
            complicationsOfPregnancyOptions,
            metarnalHealthConditionOptions,
            diabetesAndPregnancyOptions,
            hypertensionDisorderOptions,
            typeOfLabourOptions,
            augmentationOptions,
            methodsOfInductionOptions,
            typeOfDeliveryOptions,
            cervicalRipeningOptions,
            apgarOptions,
            neonatalBirthComplicationsOptions,
            indicationLabourOptions,
            indicationCaesareanOptions,
            labourBirthComplicationsOptions,
            maternalOutcomeOptions,
            medicationForCOSOptions
        } = useBornFieldOptions();

        const { isInBornPage } = toRefs(props);
        const bornForm = inject("bornForm", null);
        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.bornExtra = { ...newValue };
        });

        const medicationForCOSContainFSHOrLhOrFshPlusLHHandler = (contains) => {
            medicationForCOSContainFSHOrLhOrFshPlusLH.value = contains;
        };
        const medicationForCOSHandler = (value) => {
            medicationForCOS.value = value;
        };

        onMounted(() => {
            emitter.on("born:save-temp", save);
            emitter.on(
                "ovarian-form:medication-cos-contains-fsh-or-lh-or-fsh+lh",
                medicationForCOSContainFSHOrLhOrFshPlusLHHandler
            );
            emitter.on(
                "ovarian-form:medication-cos-changed",
                medicationForCOSHandler
            );

            getBornExtra();
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
            emitter.off(
                "ovarian-form:medication-cos-contains-fsh-or-lh-or-fsh+lh",
                medicationForCOSContainFSHOrLhOrFshPlusLHHandler
            );
            emitter.off(
                "ovarian-form:medication-cos-changed",
                medicationForCOSHandler
            );
        });

        return {
            isInBornPage,
            form,
            v$,
            save,
            loading,
            medicationForCOSOptions,
            complicationsOfPregnancyOptions,
            metarnalHealthConditionOptions,
            diabetesAndPregnancyOptions,
            hypertensionDisorderOptions,
            typeOfLabourOptions,
            augmentationOptions,
            methodsOfInductionOptions,
            typeOfDeliveryOptions,
            cervicalRipeningOptions,
            apgarOptions,
            neonatalBirthComplicationsOptions,
            indicationLabourOptions,
            indicationCaesareanOptions,
            labourBirthComplicationsOptions,
            maternalOutcomeOptions
        };
    }
};
</script>

