<template>
    <p-dialog
        header="Lab Contacts"
        v-model:visible="labContactsDailog"
        :style="{ width: '400px' }"
        :modal="true"
        @show="getMobileNumbers"
    >
        <p-data-table
            :value="mobileNumbers"
            v-model:selection="selectedMobileNumbers"
            class="p-datatable-sm"
        >
            <p-column selectionMode="multiple" headerStyle="width: 3em" />
            <p-column field="mobilenumber" header="Mobile Number" />

            <template #footer>
                <div class="p-grid">
                    <p-input-text v-model="numberInput" class="p-col p-mr-2" />
                    <p-button
                        class="p-col-fixed"
                        label="Add"
                        style="width: 80px"
                        @click="addNumber"
                    />
                </div>
            </template>
        </p-data-table>

        <template #footer>
            <p-button
                label="Delete Selected"
                icon="pi pi-times"
                @click="deleteSelected"
                class="p-button-danger"
            />
            <p-button
                label="Close"
                icon="pi pi-check"
                @click="closeModal"
                class="p-button-warning"
            />
        </template>
    </p-dialog>
</template>

<script>
import { inject, ref } from "vue";
import { $axios } from "@/service/app-http";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const labContactsDailog = inject("labContactsDailog");

        const numberInput = ref();
        const mobileNumbers = ref([]);
        const selectedMobileNumbers = ref([]);

        const addNumber = () => {
            $axios
                .post("labcontact/save", {
                    mobileNumber: numberInput.value,
                    providerNo: store.state.user.providerNo
                })
                .then((resp) => {
                    mobileNumbers.value.push({
                        mobilenumber: numberInput.value
                    });
                })
                .finally(() => {
                    numberInput.value = "";
                });
        };

        const deleteSelected = () => {
            Promise.all(
                selectedMobileNumbers.value.map((item) => {
                    return $axios.post("labcontact/delete", {
                        mobileNumber: item.mobilenumber
                    });
                })
            ).then((resp) => {
                getMobileNumbers();

            });
        };

        const getMobileNumbers = async () => {
            const { data } = await $axios.get("labcontact/list");
            mobileNumbers.value = data;
            selectedMobileNumbers.value = [];
        };

        return {
            labContactsDailog,
            numberInput,
            addNumber,
            deleteSelected,
            closeModal: () => {
                labContactsDailog.value = false;
            },
            mobileNumbers,
            selectedMobileNumbers,
            getMobileNumbers
        };
    }
};
</script>
