<template>
    <div class="container">
        <p-card>
            <template #content>
                <div style="display: flex; justify-content: space-between;">
                    <div>
                        <div>
                            <label for="cycle-type">
                                <span class="cycle-info-label">Cycle Type:</span>
                                <strong class="p-ml-2">
                                    {{ cycle.type }}
                                </strong>
                            </label>
                        </div>
                        <div>
                            <label for="cycle-no">
                                <span class="cycle-info-label">Cycle #:</span>
                                <strong class="p-ml-2">
                                    {{ cycle.no }}
                                </strong>
                            </label>
                        </div>
                        <div>
                            <label for="cycle-start-date">
                                <span class="cycle-info-label"
                                    >Cycle Start Date:</span
                                >
                                <strong class="p-ml-2">
                                    {{ showFormatedDate(cycle.date) }}
                                </strong>
                            </label>
                        </div>
                        <div>
                            <label for="cycle-cancelled">
                                <span class="cycle-info-label"
                                    >Cycle Canceled:</span
                                >
                                <strong class="p-ml-2">
                                    {{ cycle.cancelled ? "Yes" : "No" }}
                                </strong>
                            </label>
                        </div>
                        <div>
                            <label for="funded">
                                <span class="cycle-info-label">Funded : </span>
                                <strong class="p-ml-2">
                                    {{ selectedCycle?.ohip ? "Yes" : "No" }}
                                </strong>
                            </label>
                        </div>
                        <div>
                            <label class="p-mb-0">
                                <span style="font-size: 14px">LMP Date</span>
                            </label>
                            <p-calendar
                                style="width: 100px;"
                                v-model="summaryForm.lmpDate"
                                dateFormat="mm/dd/y"
                                :showIcon="true"
                            />
                        </div>
                        <div class="p-mt-1">
                            <label class="p-mb-0">
                                <span style="font-size: 14px">Length in Days</span>
                            </label>
                            <p-input-text
                                v-model="summaryForm.lengthInDays"
                                style="width: 40px;"
                            />
                        </div>
                    </div>
                    <div
                        style="display: flex; flex-direction: column; align-items: flex-end;"
                    >
                        <div style="display: flex; align-items: center;">
                            <label
                                for="cancel"
                                style="font-weight: bold; font-size: 16px;"
                            >
                                Cancel
                            </label>
                            <div class="p-ml-2">
                                <p-checkbox
                                    v-model="cycle.cycleCancelCheck"
                                    :binary="true"
                                />
                            </div>
                        </div>
                        <div
                            style="margin-top: 58px; font-weight: bold; font-size: 10px;"
                            v-html="formattedDonnations"
                        ></div>
                    </div>
                </div>
            </template>
            <template #footer>
                <cycle-nav/>
            </template>
        </p-card>
    </div>
</template>

<script>
import {
    onMounted,
    reactive,
    ref,
    watch,
    computed,
    inject,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";
import { showFormatedDate } from "@/service/utils";
import { $axios } from "@/service/app-http";

import CycleNav from "@/components/shared/cycle-nav.vue";
import { TIME_0 } from "@/constants";
import { useToast } from "primevue/usetoast";

export default {
    components: {
        CycleNav
    },
    setup() {
        const store = useStore();
        const toast = useToast();

        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );

        const checkScannedBarcode = async (_barcode) => {
            if (cyclesSummary.value) {
                const selectedCycleSummery =
                    selectedCycle.value &&
                    selectedCycle.value.cycleId.toString() === _barcode
                        ? selectedCycle.value
                        : cyclesSummary.value.find(
                              (row) => row.cycleId.toString() === _barcode
                          );

                // Output the result
                if (selectedCycleSummery) {
                    emitter.emit("cycles-ui:play-beep", 1);
                    // Example: Use toast to display the value
                    toast.add({
                        severity: "success",
                        summary: _barcode,
                        detail: "Barcode is correct. Confirmed!",
                        life: 3000
                    });

                    if (
                        selectedCycle.value &&
                        selectedCycle.value.cycleId.toString() === _barcode
                    ) {
                    } else {
                        store.commit(
                            "artCycle/SET_SELECTED_CYCLE",
                            selectedCycleSummery
                        );
                        store.dispatch("born/getBronV2", selectedCycleSummery);
                    }
                } else {
                    if (
                        store.state.artCycle.patient &&
                        store.state.artCycle.patient.chart == _barcode
                    ) {
                        emitter.emit("cycles-ui:play-beep", 1);
                        toast.add({
                            severity: "warn",
                            summary: _barcode,
                            detail: "Patient is correct. Confirmed!",
                            life: 3000
                        });

                        return;
                    }

                    emitter.emit("cycles-ui:play-beep", 0);
                    toast.add({
                        severity: "error",
                        summary: _barcode,
                        detail: "Barcode is not correct. Wrong!",
                        life: 3000
                    });

                    const scanarea = "art-cycle";

                    try {
                        const { data } = await $axios.get(
                            `scan-barcode/data?barcode=${_barcode}&scanarea=${scanarea}`
                        );

                        emitter.emit("cycles-ui:redict-to-other", data);
                    } catch (e) {
                        console.log(e);
                    }

                    // const anotherPatient = '105337';
                    // const pageName = 'blood-tests';
                }
            }
            // Example: Log the value
            // console.log("Scanned barcode:", _barcode);
        };

        const summaryForm = inject("summaryForm");
        const cycle = reactive({
            type: null,
            no: null,
            donnations: null,
            cancelled: false,
            cycleCancelCheck: false,
            funded: false,
            date: null
        });
        const rules = {
            type: { required },
            no: { required }
        };
        const v$ = useVuelidate(rules, cycle);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.cycleInfoForm = !v$.value.$invalid;
        };

        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        watch(monitorDetails, (newValue) => {
            if (newValue) {
                summaryForm.lmpDate = newValue.lmpDate
                    ? new Date(newValue.lmpDate + TIME_0)
                    : null;
                summaryForm.lengthInDays = newValue.lengthInDays;
                summaryForm.donnations = newValue.donnations;
            } else {
                summaryForm.lmpDate = null;
                summaryForm.lengthInDays = null;
                summaryForm.donnations = null;
            }
        });

        watch(cycle, (newValue) => {
            summaryForm.type = newValue.type;
            summaryForm.no = newValue.no;
            summaryForm.donnations = newValue.donnations;
            summaryForm.cancelled = newValue.cancelled;
            summaryForm.cycleCancelCheck = newValue.cycleCancelCheck;
            summaryForm.funded = newValue.funded;
            summaryForm.date = newValue.date;
        });

        const disable = ref(false);

        const fillCycleInfo = () => {
            let _cycle = store.state.artCycle.selectedCycle;

            // console.log(store.state.artCycle.selectedCycle);
            if (_cycle) {
                cycle.type = _cycle.cycleType;
                cycle.no = _cycle.cycleNo;
                cycle.donnations = _cycle.donnations;
                cycle.cancelled = _cycle.cancel;
                cycle.cycleCancelCheck = cycle.cancelled;
                cycle.date = _cycle.cycleDate
                    ? _cycle.cycleDate.split(" ")[0]
                    : null;
                disable.value = true;
            }
        };

        const formattedDonnations = computed(() => {
            if (summaryForm.donnations) {
                // Split the donnations string by <br> to separate into individual donations
                const donnationsArray = summaryForm.donnations.split("<br>");

                // Map over each donation and format it
                return donnationsArray
                    .map((donnation) => {
                        return `<div>${donnation}</div>`;
                    })
                    .join(""); // Join the formatted donations into a single string
            } else {
                return ""; // Return an empty string if donnations is null or undefined
            }
        });

        watch(selectedCycle, (newValue) => {
            fillCycleInfo();
        });

        onMounted(() => {
            fillCycleInfo();
            emitter.on("cycles-ui:check-barcode", checkScannedBarcode);
            emitter.on("cycle-monitoring-toolbar:save", onSave);
            console.log("Cycle Lengths:", summaryForm.lengthInDays);
        });

        onUnmounted(() => {
            emitter.off("cycles-ui:check-barcode", checkScannedBarcode);
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return {
            cycle,
            disable,
            submitted,
            v$,
            selectedCycle,
            showFormatedDate,
            summaryForm,
            formattedDonnations
        };
    }
};
</script>

<style scoped>
.cycle-info-label {
    color: #000000bf;
}
@media (max-width: 768px) {
    .container {
        margin-top: 50px;
    }
}
</style>
