<template>
    <p-card class="p-mt-2">
        <template #content>
            <p-divider align="left" style="margin-top: 0">
                <b>
                    Ovarian Reserve-Oocyte Provider / Age of Oocyte Provider:
                    age calculted using patient's DOB; update if patient not
                    oocyte provider (e.g. donor egg case)
                </b>
            </p-divider>

            <div class="p-formgroup-inline">
                <div class="p-field">
                    <label>Age</label>
                    <p-input-text
                        v-model="form.ageOfOocyteAge"
                        class="p-col-fixed"
                        style="width: 50px"
                        type="number"
                        :class="{
                            'p-invalid': v$.ageOfOocyteAge.$invalid
                        }"
                        min="0"
                    />
                    <i
                        v-if="v$.ageOfOocyteAge.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.ageOfOocyteAge.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">Height(cm)</label>
                    <p-input-number
                        id="height-of-oocytye"
                        v-model="form.hightOfOocyteHight"
                        :class="{
                            'p-invalid': v$.hightOfOocyteHight.$invalid
                        }"
                    />
                    <i
                        v-if="v$.hightOfOocyteHight.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.hightOfOocyteHight.minValue.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">Weight(kg)</label>
                    <p-input-number
                        id="weight-of-oocyte"
                        v-model="form.weightOfOocyte"
                        mode="decimal"
                        :minFractionDigits="1"
                        :maxFractionDigits="1"
                        :class="{
                            'p-invalid': v$.weightOfOocyte.$invalid
                        }"
                    />
                    <i
                        v-if="v$.weightOfOocyte.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.weightOfOocyte.minValue.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label>
                        BMI(kg/m<sup>2</sup>)
                    </label>
                    <p-input-number
                        id="bmi-of-oocyte"
                        v-model="form.bmiOfOocyte"
                        mode="decimal"
                        :minFractionDigits="1"
                        :maxFractionDigits="1"
                        :class="{
                            'p-invalid': v$.bmiOfOocyte.$invalid
                        }"
                    />
                    <i
                        v-if="v$.bmiOfOocyte.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.bmiOfOocyte.minValue.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <!-- <label >FSH day 2-4</label>
                <p-input-text
                    v-model="form.fshDayOocyte"
                    class="p-col-fixed"
                    style="width: 50px"
                    type="number"
                /> -->

                <div class="p-field">
                    <label>AFC</label>
                    <p-input-number
                        id="afc-of-oocyte"
                        v-model="form.afcOocyte"
                        :class="{
                            'p-invalid': v$.afcOocyte.$invalid
                        }"
                    />
                    <i
                        v-if="v$.afcOocyte.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.afcOocyte.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label>AMH Level</label>
                    <p-input-number
                        id="amh-level-oocyte"
                        v-model="form.amhLevelOocyte"
                        mode="decimal"
                        :minFractionDigits="1"
                        :maxFractionDigits="1"
                        :max="9999.99"
                        :class="{
                            'p-invalid': v$.amhLevelOocyte.$invalid
                        }"
                    />
                    <i
                        v-if="v$.amhLevelOocyte.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.amhLevelOocyte.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <!-- <label >ng/ml pmo/1</label>
                <br />
                <p-toggle-button
                    v-model="form.ngPMOOocyte"
                    :binary="true"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                /> -->

                <div class="p-field-checkbox">
                    <label>Cycle Stopped</label>
                    <p-radio-button value="Y" v-model="form.cycleStopped" />
                    <label>Yes</label>
                </div>
                <div class="p-field-checkbox">
                    <p-radio-button value="N" v-model="form.cycleStopped" />
                    <label>NO</label>
                </div>
            </div>

            <p-divider align="left" style="margin-top: 0">
                <b>Simulation Protocol-Oocyte Provider</b>
            </p-divider>

            <div class="p-formgroup-inline">
                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Type of Simulation Protocol
                    </label>
                    <p-dropdown
                        v-model="form.typeOfStimulationProtocol"
                        :options="typeOfStimulationProtocolOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width:250px"
                    />
                </div>
                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Other stimulation protocol desc
                    </label>
                    <p-input-text v-model="form.otherTypeOfStiProtocolDes" />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Medication for Controlled Ovarian Stimulation (COS)
                    </label>
                    <p-multi-select
                        v-model="form.medicationForCOS"
                        :options="medicationForCOSOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width:250px"
                        :class="{
                            'p-invalid': v$.medicationForCOS.$invalid
                        }"
                    />

                    <i
                        v-if="v$.medicationForCOS.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.medicationForCOS.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Other Medication for COS
                    </label>
                    <p-input-text v-model="form.otherMedicationCOS" />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Adjuvant Medication
                    </label>
                    <p-multi-select
                        v-model="form.adjuvantMedication"
                        :options="adjuvantMedicationOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width: 200px"
                    />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Other Adjuvant Med. desc
                    </label>
                    <p-input-text v-model="form.otherAdjuvantMediDescription" />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Trigger Medication
                    </label>
                    <p-dropdown
                        v-model="form.triggerMedication"
                        :options="triggerMedicationOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Other Trigger Medication Desc
                    </label>
                    <p-input-text v-model="form.otheTriMedDesc" />
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        No of Days of gonadropins
                    </label>
                    <p-input-number
                        v-model="form.noOfDaysGonadotropins"
                        :class="{
                            'p-invalid': v$.noOfDaysGonadotropins.$invalid
                        }"
                        :max="99"
                    />
                    <i
                        v-if="v$.noOfDaysGonadotropins.$invalid"
                        class="pi pi-info-circle p-ml-2"
                        v-tooltip.right="
                            v$.noOfDaysGonadotropins.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Total dose of FSH
                    </label>
                    <p-input-number
                        v-model="form.totalDoseFSH"
                        :class="{
                            'p-invalid': v$.totalDoseFSH.$invalid
                        }"
                        :max="99999"
                    />
                    <i
                        v-if="v$.totalDoseFSH.$invalid"
                        class="pi pi-info-circle p-ml-2"
                        v-tooltip.right="v$.totalDoseFSH.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field p-mt-1">
                    <label>Total dose of FSH Rekovelle in mcg</label>
                    <p-input-number
                        v-model="form.totalDoseRekovelleMcg"
                        :class="{
                            'p-invalid': v$.totalDoseRekovelleMcg.$invalid
                        }"
                        mode="decimal"
                        :minFractionDigits="2"
                        :maxFractionDigits="2"
                        :max="999.99"
                    />
                    <i
                        v-if="v$.totalDoseRekovelleMcg.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.totalDoseRekovelleMcg.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        Total dose of LH
                    </label>

                    <p-input-number
                        v-model="form.totalDoseLH"
                        :class="{
                            'p-invalid': v$.totalDoseLH.$invalid
                        }"
                        :minFractionDigits="1"
                        :maxFractionDigits="1"
                        :max="999.9"
                    />
                    <i
                        v-if="v$.totalDoseLH.$invalid"
                        class="pi pi-info-circle p-ml-2"
                        v-tooltip.right="v$.totalDoseLH.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field p-mt-1">
                    <label>
                        Medication administered prior Controlled Ovarian
                        Stimulation (COS)
                    </label>
                    <p-multi-select
                        v-model="form.mediAdminisPriorCos"
                        :options="medicationAdministraionPriorToCosOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width: 250px"
                        :class="{
                            'p-invalid': v$.mediAdminisPriorCos.$invalid
                        }"
                    />
                    <i
                        v-if="v$.mediAdminisPriorCos.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.mediAdminisPriorCos.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field p-mt-1">
                    <label>Other medication prior cos desc</label>
                    <p-input-text
                        v-model="form.otherMediPriorCosDesc"
                        type="text"
                    />
                </div>
            </div>

            <p-divider align="left" style="margin-top: 0">
                <b>Ovarian Response-Oocyte Provider</b>
            </p-divider>

            <div class="p-formgroup-inline">
                <div class="p-field">
                    <label class="p-col-fixed p-mb-0">
                        # of follicles &lt; 15mm
                    </label>
                    <p-input-number
                        v-model="form.ofFolliclesLs15mm"
                        :max="999"
                        v-tooltip.right="
                            v$.ofFolliclesLs15mm.validator.$message
                        "
                        :class="{
                            'p-invalid': v$.ofFolliclesLs15mm.$invalid
                        }"
                    />
                    <i
                        v-if="v$.ofFolliclesLs15mm.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.ofFolliclesLs15mm.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        # of follicles &gt; 15mm
                    </label>
                    <p-input-number
                        v-model="form.ofFolliclesGt15mm"
                        v-tooltip.right="
                            v$.ofFolliclesGt15mm.validator.$message
                        "
                        :class="{
                            'p-invalid': v$.ofFolliclesGt15mm.$invalid
                        }"
                    />
                    <i
                        v-if="v$.ofFolliclesGt15mm.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.ofFolliclesGt15mm.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        Estradiol level
                    </label>
                    <p-input-number
                        v-model="form.estradiolLevel"
                        :minFractionDigits="2"
                        :maxFractionDigits="2"
                        :class="{
                            'p-invalid': v$.estradiolLevel.$invalid
                        }"
                    />
                    <i
                        v-if="v$.estradiolLevel.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.estradiolLevel.maxValue.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        Progesterone Level
                    </label>
                    <p-input-number
                        v-model="form.progesterOneLevel"
                        :minFractionDigits="1"
                        :maxFractionDigits="1"
                        :class="{
                            'p-invalid': v$.progesterOneLevel.validator.$invalid
                        }"
                    />
                    <i
                        v-if="v$.progesterOneLevel.validator.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.progesterOneLevel.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        Endometrial thickness Fresh Cycle
                    </label>
                    <p-input-number
                        v-model="form.endometrialThicknessFreshCycle"
                        :minFractionDigits="2"
                        :maxFractionDigits="2"
                        :max="9999999999.99"
                    />
                </div>

                <div class="p-field-checkbox">
                    <label class="p-col-fixed p-mb-2">
                        Cycle Cancelled before retrieval
                    </label>
                    <p-radio-button
                        v-model="form.cycleCancelledBeforeRetrieval"
                        name="provincial-funding"
                        value="yes"
                    />
                    <label>Yes</label>
                    <p-radio-button
                        v-model="form.cycleCancelledBeforeRetrieval"
                        name="provincial-funding"
                        value="no"
                    />
                    <label>NO</label>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        Reason Cancelled
                    </label>

                    <p-multi-select
                        v-model="form.reasonCancelled"
                        :options="reasonCancelledOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width:250px"
                        :class="{
                            'p-invalid': v$.reasonCancelled.validator.$invalid
                        }"
                        :disabled="form.cycleCancelledBeforeRetrieval == 'no'"
                    />

                    <i
                        v-if="v$.reasonCancelled.validator.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="v$.reasonCancelled.validator.$message"
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field ">
                    <label class="p-col-fixed p-mb-0">
                        Other reason cancelled description
                    </label>
                    <p-input-text
                        v-model="form.otherReasonCanDesc"
                        :class="{
                            'p-invalid':
                                v$.otherReasonCanDesc.validator.$invalid
                        }"
                        :disabled="form.cycleCancelledBeforeRetrieval == 'no'"
                    />

                    <i
                        v-if="v$.otherReasonCanDesc.validator.$invalid"
                        class="pi pi-info-circle p-ml-2 p-mt-2"
                        v-tooltip.right="
                            v$.otherReasonCanDesc.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                :disabled="v$.$invalid"
            />

            <p-button
                icon="pi pi-save"
                label="Mark As Completed"
                class="p-ml-2"
                @click="markAsComplete('cycle-monitoring')"
            />
        </template>
    </p-card>
</template>

<script>
import { inject, onMounted, onUnmounted, toRefs, watch } from "vue";
import { useBornMarkAsComplete, useOvariannFormInit } from "@/composable/born";
import { emitter } from "@/event-bus";
import {
    FSH_REKOVELLE_VALIDATORS_MEDICATION_FOR_COS,
    FSH_VALIDATORS_MEDICATION_FOR_COS
} from "@/constants";
export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            loading,
            form,
            v$,
            save,
            fillForm,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            medicationAdministraionPriorToCosOptions,
            reasonCancelledOptions,
            adjuvantMedicationOptions
        } = useOvariannFormInit();

        const { markAsComplete } = useBornMarkAsComplete();

        watch(() => store.state.born.ovarian, fillForm);

        const { isInBornPage } = toRefs(props);
        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.ovarian = { ...newValue };
        });

        watch(
            () => form.medicationForCOS,
            (newVal) => {
                emitter.emit(
                    "ovarian-form:medication-cos-contains-fsh-or-lh-or-fsh+lh",
                    newVal.includes(4) ||
                        newVal.includes(5) ||
                        newVal.includes(6)
                );
                emitter.emit(
                    "ovarian-form:medication-cos-changed",
                    form.medicationForCOS
                );
                let intersectionFSH = [];
                intersectionFSH = newVal.filter(
                    (item) => !FSH_VALIDATORS_MEDICATION_FOR_COS.includes(item)
                );
                if (intersectionFSH.length > 0) {
                    // form.totalDoseFSH = null;
                }

                let intersectionFSHRekovelle = [];
                intersectionFSHRekovelle = newVal.filter(
                    (item) =>
                        !FSH_REKOVELLE_VALIDATORS_MEDICATION_FOR_COS.includes(
                            item
                        )
                );
                if (intersectionFSHRekovelle.length > 0) {
                    // form.totalDoseRekovelleMcg = null;
                }
            }
        );

        watch(
            () => [form.hightOfOocyteHight, form.weightOfOocyte],
            ([height, weight]) => {
                if (height != null && weight != null) {
                    let heightInMeter = height / 100;
                    form.bmiOfOocyte = parseFloat(
                        (weight / (heightInMeter * heightInMeter)).toFixed(1)
                    );
                }
            }
        );

        watch(
            () => form.cycleCancelledBeforeRetrieval,
            (newVal) => {
                store.commit("born/SET_CYCLE_CANCELLED", newVal);
                if (newVal == "no") {
                    form.reasonCancelled = [];
                    form.otherReasonCanDesc = null;
                }
            }
        );

        watch(
            () => store.getters["cycleMonitoring/latestLine"],
            (line) => {
                if (
                    (form.estradiolLevel == null ||
                        form.estradiolLevel === "") &&
                    line &&
                    line.e2 != null &&
                    line.e2 != ""
                ) {
                    form.estradiolLevel = parseFloat(line.e2);
                }
                if (
                    (form.progesterOneLevel == null ||
                        form.progesterOneLevel === "") &&
                    line &&
                    line.prog != null &&
                    line.prog != ""
                ) {
                    form.progesterOneLevel = parseFloat(line.prog);
                }
                if (
                    (form.endometrialThicknessFreshCycle == null ||
                        form.endometrialThicknessFreshCycle === "") &&
                    line &&
                    line.etUs != null &&
                    line.etUs != ""
                ) {
                    form.endometrialThicknessFreshCycle = parseFloat(line.etUs);
                }
                if (
                    (form.totalDoseLH == null || form.totalDoseLH === "") &&
                    line &&
                    line.lh != null &&
                    line.lh != ""
                ) {
                    form.totalDoseLH = parseFloat(line.lh);
                }
            }
        );

        onMounted(() => {
            emitter.on("born:save-temp", save);

            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        return {
            isInBornPage,
            form,
            v$,
            save,
            loading,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            medicationAdministraionPriorToCosOptions,
            reasonCancelledOptions,
            adjuvantMedicationOptions,
            markAsComplete
        };
    }
};
</script>

<style>
#height-of-oocytye,
#weight-of-oocyte,
#bmi-of-oocyte,
#afc-of-oocyte,
#amh-level-oocyte {
    width: 50px;
}
</style>
