<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12 p-formgroup-inline formgroup1">
                    <!-- <div class="p-field-checkbox">
                        <p-checkbox v-model="form.pgs" :binary="true" />
                        <label for="city3">PGS</label>
                    </div> -->

                    <!-- <div class="p-field-checkbox">
                        <p-checkbox v-model="form.pgd" :binary="true" />
                        <label for="city3">PGD</label>
                    </div> -->

                    <!-- <div class="p-field-checkbox">
                        <p-checkbox v-model="form.tese" :binary="true" />
                        <label for="city4">TESE</label>
                    </div> -->

                    <!-- <div class="p-field-checkbox">
                        <p-checkbox v-model="form.caIonaphore" :binary="true" />
                        <label for="city3">Ca lanophoree</label>
                    </div> -->

                    <!-- <div class="p-field-checkbox">
                        <p-checkbox v-model="form.ah" :binary="true" />
                        <label for="city4">AH</label>
                    </div> -->

                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.labAware" :binary="true" />
                        <label for="city4">Lab Aware</label>
                    </div>

                    <div class="p-field p-grid">
                        <label for="sbw-f" class="p-col-fixed p-mb-0">
                            SBW F
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.sbwf"
                                id="sbw-f"
                                dateFormat="mm/dd/y"
                                :showIcon="true"
                            >
                            </p-calendar>
                        </div>
                    </div>

                    <div class="p-field p-grid">
                        <label for="sbw-m" class="p-col-fixed p-mb-0">
                            SBW M
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.sbwm"
                                id="sbw-m"
                                dateFormat="mm/dd/y"
                                :showIcon="true"
                            />
                        </div>
                    </div>

                    <div class="p-field p-grid">
                        <label for="sono" class="p-col-fixed p-mb-0">
                            SONO
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.sono"
                                id="sono"
                                dateFormat="mm/dd/y"
                                :showIcon="true"
                            />
                        </div>
                    </div>

                    <div class="p-field p-grid">
                        <label for="lab-comment" class="p-col-fixed p-mb-0">
                            Lab Comment
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.labComment"
                                id="lab-comment"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col p-formgroup-inline formgroup2">
                    <div class="p-field p-grid">
                        <!-- drugcover -->
                        <div class="covered">
                            <div class="p-col-fixed">
                                <p-checkbox
                                    v-model="form.drugCover"
                                    :binary="true"
                                />
                            </div>
                            <label for="drugCover" class="p-col-fixed p-mb-0">
                                Covered
                            </label>
                        </div>
                        <div class="fees">
                            <label for="fees" class="p-col-fixed p-mb-0">
                                Fees
                            </label>
                            <div class="p-col">
                                <p-input-text
                                    v-model="form.fees"
                                    id="fees"
                                    type="text"
                                />
                            </div>
                        </div>
                        <div class="gestationalSac">
                            <label for="gestationalSac" class="p-col-fixed p-mb-0">
                                Gestational Sac
                            </label>
                            <div class="p-col">
                                <p-input-text
                                    v-model="form.gestationalSac"
                                    id="gestationalSac"
                                    type="text"
                                />
                            </div>
                        </div>
                        <div class="heartBeat">
                            <label for="heartBeat" class="p-col-fixed p-mb-0">
                                Heart Beat
                            </label>
                            <div class="p-col">
                                <p-input-text
                                    v-model="form.heartBeat"
                                    id="heartBeat"
                                    type="text"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    computed,
    inject,
    ref,
    reactive,
    watch,
    onMounted,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { TIME_0 } from "@/constants";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const summaryForm = inject("summaryForm");
        const form = reactive({
            hcgiu: false,
            intralipid: false,
            pgs: false,
            pgd: false,
            tese: false,
            fragmin: false,
            neurogen: false,
            embryoGlue: false,
            caIonaphore: false,
            ah: false,
            labAware: false,
            sbwf: null,
            sbwm: null,
            sono: null,
            labComment: null,
            drugCover: false,
            fees: null,
            gestationalSac: null,
            heartBeat: null
        });

        watch(form, (newValue) => {
            summaryForm.hcgiu = newValue.hcgiu;
            summaryForm.intralipid = newValue.intralipid;

            summaryForm.pgd = newValue.pgd;
            summaryForm.fragmin = newValue.fragmin;
            summaryForm.neurogen = newValue.neurogen;
            summaryForm.embryoGlue = newValue.embryoGlue;

            summaryForm.ah = newValue.ah;
            summaryForm.labAware = newValue.labAware;
            summaryForm.drugCover = newValue.drugCover;
            summaryForm.fees = newValue.fees;
            summaryForm.gestationalSac = newValue.gestationalSac;
            summaryForm.heartBeat = newValue.heartBeat;
            try {
                summaryForm.sbwf = newValue.sbwf
                    ? format(newValue.sbwf, DATE_FORMAT_STRING)
                    : null;
            } catch {
                summaryForm.sbwf = newValue.sbwf;
            }
            try {
                summaryForm.sbwm = newValue.sbwm
                    ? format(newValue.sbwm, DATE_FORMAT_STRING)
                    : null;
            } catch {
                summaryForm.sbwm = newValue.sbwm;
            }
            try {
                summaryForm.sono = newValue.sono
                    ? format(newValue.sono, DATE_FORMAT_STRING)
                    : null;
            } catch {
                summaryForm.sono = newValue.sono;
            }
            summaryForm.labComment = newValue.labComment;
        });

        watch(monitorDetails, (newValue) => {
            form.hcgiu = !!newValue.hcgiu;
            form.intralipid = !!newValue.intralipid;
            form.pgs = !!newValue.pgs;
            form.pgd = !!newValue.pgd;
            form.tese = !!newValue.tese;
            form.fragmin = !!newValue.fragmin;
            form.neurogen = !!newValue.neurogen;
            form.embryoGlue = !!newValue.embryoGlue;
            form.caIonaphore = !!newValue.caIonaphore;
            form.ah = !!newValue.ah;
            form.labAware = !!newValue.labAware;
            form.drugCover = newValue.drugCover === "1" ? true : false;
            form.fees = newValue.fees;
            form.gestationalSac = newValue.gestationalSac;
            form.heartBeat = newValue.heartBeat;
            // form.sbwf = newValue.sbwf ? new Date(newValue.sbwf + TIME_0) : null;
            // form.sbwm = newValue.sbwm ? new Date(newValue.sbwm + TIME_0) : null;
            // form.sono = newValue.sono ? new Date(newValue.sono + TIME_0) : null;

            form.sbwf = newValue.sbwf;
            form.sbwm = newValue.sbwm;
            form.sono = newValue.sono;

            form.labComment = newValue.labComment;

            submitted.value = false;
        });

        const rules = {};
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.therapyForm = true;
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { form, v$, submitted };
    }
};
</script>
<style scoped>
.covered {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.fees {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.gestationalSac {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.heartBeat {
    display: flex;
    flex-direction: row;
    align-items: center;
}
@media (max-width: 768px) {
    .formgroup1 {
        display: flex;
        flex-direction: column;
    }
    .formgroup2 {
        display: flex;
        flex-direction: column;
    }
    .covered {
        display: flex;
        flex-direction: row;
    }
    .fees {
        display: flex;
        flex-direction: row;
        margin-bottom: 4px;
    }
    .gestationalSac {
        display: flex;
        flex-direction: row;
        margin-bottom: 4px;
    }
    .heartBeat {
        display: flex;
        flex-direction: row;
    }
}
</style>
