<template>
    <p-toolbar class="p-p-1 toolbar">
        <template #left>
            <div class="toolbar-left">
                <p-button
                    label="Save"
                    class="p-mr-4"
                    @click="save"
                    v-if="hasUpdatePermission || hasCreatePermission"
                />

                <p-divider layout="vertical" />
                <span>
                    FSH:
                    <strong
                        :style="
                            isStyleRed('FSH', bloodTests.fsh)
                                ? { color: 'red' }
                                : {}
                        "
                        >{{ bloodTests.fsh }}</strong
                    >
                </span>
                <p-divider layout="vertical" />
                <span>
                    TSH:
                    <strong
                        :style="
                            isStyleRed('TSH', bloodTests.tsh)
                                ? { color: 'red' }
                                : {}
                        "
                        >{{ bloodTests.tsh }}</strong
                    >
                </span>
                <p-divider layout="vertical" />
                <span>
                    PRL:
                    <strong
                        :style="
                            isStyleRed('PRL', bloodTests.prl)
                                ? { color: 'red' }
                                : {}
                        "
                        >{{ bloodTests.prl }}</strong
                    >
                </span>
                <p-divider layout="vertical" />
                <span>
                    BhCG:
                    <strong
                        :style="
                            isStyleRed('BHCG', bloodTests.bhcg)
                                ? { color: 'red' }
                                : {}
                        "
                        >{{ bloodTests.bhcg }}</strong
                    >
                </span>
                <p-divider layout="vertical" />
                <span>
                    TESTO: <strong>{{ bloodTests.testo }}</strong>
                </span>
                <p-divider layout="vertical" />
                <span>
                    DHEAS:
                    <strong
                        :style="
                            isStyleRed('DHEAS', bloodTests.dheas)
                                ? { color: 'red' }
                                : {}
                        "
                        >{{ bloodTests.dheas }}</strong
                    >
                </span>
                <p-divider layout="vertical" />
                <span>
                    AMHP: <strong>{{ bloodTests.amh }}</strong>
                </span>
                <!-- <p-divider layout="vertical" />
                <span>
                    Fetus: <strong>{{ monitorDetails.no_fetus }}</strong>
                </span> -->
                <p-divider layout="vertical" />
                <span>
                    GS: <strong>{{ monitorDetails.gestationalSac }}</strong>
                </span>
                <p-divider layout="vertical" />
                <span>
                    HB: <strong>{{ monitorDetails.heartBeat }}</strong>
                </span>
                <p-divider layout="vertical" />
                <span>
                    Loss: <strong>{{ monitorDetails.lossFetus }}</strong>
                </span>
            </div>
        </template>

        <template #right>
            <div class="toolbar-right">
                <p-button
                    label="Cancel Message Lab"
                    class="p-mr-2 app-button p-button-sm"
                />
                <p-button
                    label="Lab Contacts"
                    class="app-button p-button-sm"
                    @click="openLabContactsDailog"
                />
            </div>
        </template>
    </p-toolbar>
</template>

<script>
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import { computed, inject } from "vue";
import { usePermissionCheck } from "@/composable/users";
import { useBloodTestUtil } from "@/composable/blood-test-util";

export default {
    setup() {
        const store = useStore();

        const bloodTests = computed(
            () => store.state.cycleMonitoring.bloodTests
        );

        const monitorDetails = computed(
            () => store.state.cycleMonitoring.monitorDetails
        );

        const labContactsDailog = inject("labContactsDailog");

        const {
            hasUpdatePermission,
            hasCreatePermission
        } = usePermissionCheck();

        const { isStyleRed } = useBloodTestUtil();

        return {
            bloodTests,
            monitorDetails,
            save: () => {
                emitter.emit("cycle-monitoring-toolbar:save");
            },
            openLabContactsDailog: () => {
                labContactsDailog.value = true;
            },
            hasUpdatePermission,
            hasCreatePermission,
            isStyleRed
        };
    }
};
</script>

<style scoped>
strong {
    color: #0025cc;
    font-size: 14px;
}

#cycle-monitoriing .p-toolbar {
    border: 1px solid white;
}

#cycle-monitoriing .p-divider.p-divider-vertical:before {
    border-color: white;
}
.toolbar-left,
.toolbar-right {
    display: flex;
    align-items: center;
}

@media (max-width: 768px) {
    .toolbar-left,
    .toolbar-right {
        flex-direction: column;
        align-items: flex-start;
    }
    .toolbar-left p-divider {
        display: none;
    }
}
</style>
