<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <!-- drugcover -->
                        <div class="p-col-fixed">
                            <p-checkbox
                                v-model="form.drugCover"
                                :binary="true"
                            />
                        </div>
                        <label for="drugCover" class="p-col p-mb-0">
                            Covered
                        </label>
                    </div>
                </div>
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <!-- Fees -->
                        <label for="fees" class="p-col-fixed p-mb-0">
                            Fees
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.fees"
                                id="fees"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, reactive, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const summaryForm = inject("summaryForm");
        const form = reactive({
            drugCover: false,
            fees: null
        });

        watch(form, (newValue) => {
            summaryForm.drugCover = newValue.drugCover;
            summaryForm.fees = newValue.fees;
        });

        watch(monitorDetails, (newValue) => {
            form.drugCover = newValue.drugCover === "1" ? true : false;
            form.fees = newValue.fees;
        });

        return { form };
    }
};
</script>
