<template>
    <main id="cycle-monitoriing" class="p-inputtext-sm p-input-filled">
        <div v-if="hasNoCycle">
            <h1 style="color: red; text-align: left; margin-top: 100px;">
                No Cycle Found
            </h1>
        </div>
        <div v-else>
            <div class="p-grid">
                <div class="p-col-12 p-md-2">
                    <cycle-monitoring-cycle-info />
                </div>

                <div class="p-col-12 p-md-7">
                    <!-- <cycle-monitoring-detail-form /> -->
                    <cycle-monitoring-stimulation-form />
                </div>

                <div class="p-col-12 p-md-3">
                    <cycle-monitoring-diagnosis-form />
                </div>

                <!-- <div class="p-col-12 p-md-1">
                    <cycle-monitoring-other-drugs-form />
                </div> -->
            </div>

            <div class="p-grid">
                <div class="p-col-12 p-md-2">
                    <cycle-monitoring-other-drugs-form />
                </div>
                <div class="p-col-12 p-md-7">
                    <cycle-monitoring-therapy-from />
                </div>

                <!-- <div class="p-col-12 p-md-2">
                <cycle-monitoring-fees-form />
            </div> -->

                <div class="p-col-12 p-md-3">
                    <cycle-monitoring-plans-form />
                </div>
            </div>

            <p-card class="group-box p-mb-2">
                <template #content>
                    <cycle-monitoring-toolbar />

                    <cycle-monitoring-line-table />

                    <!-- <cycle-monitoring-cycle-nav /> -->

                    <cycle-monitoring-comments />
                </template>
            </p-card>

            <cycle-monitoring-line-toolbar />

            <cycle-monitoring-line-edit-or-create-dialog />

            <cycle-monitoring-deleted-day-dialog />

            <template v-if="isBornCycle">
                <born-obstetrical-history-form />
                <born-ovarian-form />
                <!-- <bron-extra-form /> -->
            </template>

            <order-blood-test-confirm-dialog />
            <order-blood-test-dialog />
            <lab-contacts-dailog />
        </div>
    </main>
</template>

<script>
import { computed, onMounted, onUnmounted, provide, ref, watch } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import {
    useCycleMonitoringForm,
    useFormatToMonitorSummaryReqBody
} from "@/composable/cycle-monitoring";

import CycleMonitoringCycleNav from "@/components/cycle-monitoring/cyccle-nav.vue";
import CycleMonitoringCycleInfo from "@/components/cycle-monitoring/cycle-info.vue";
import CycleMonitoringDetailForm from "@/components/cycle-monitoring/detail-form.vue";
import CycleMonitoringDiagnosisForm from "@/components/cycle-monitoring/diagnosis-form.vue";
import CycleMonitoringOtherDrugsForm from "@/components/cycle-monitoring/other-durgs-form.vue";
import CycleMonitoringTherapyFrom from "@/components/cycle-monitoring/therapy-form.vue";
import CycleMonitoringLineTable from "@/components/cycle-monitoring/line-table.vue";
import CycleMonitoringComments from "@/components/cycle-monitoring/comments.vue";
import CycleMonitoringFeesForm from "@/components/cycle-monitoring/fees-form.vue";
import CycleMonitoringPlansForm from "@/components/cycle-monitoring/plans-form.vue";
import CycleMonitoringToolbar from "@/components/cycle-monitoring/toolbar.vue";
import CycleMonitoringLineToolbar from "@/components/cycle-monitoring/line-toolbar.vue";
import CycleMonitoringLineEditOrCreateDialog from "@/components/cycle-monitoring/line-edit-or-create-dialog.vue";
import CycleMonitoringStimulationForm from "@/components/cycle-monitoring/stimulation-form.vue";
import CycleMonitoringDeletedDayDialog from "@/components/cycle-monitoring/deleted-day-dialog.vue";

import BornObstetricalHistoryForm from "@/components/born/obstetrical-history-form.vue";
import BornOvarianForm from "@/components/born/ovarian-form.vue";
import BronExtraForm from "@/components/born/extra-form.vue";

import OrderBloodTestConfirmDialog from "@/components/blood-tests/confirm-dialog.vue";
import OrderBloodTestDialog from "@/components/blood-tests/dialog.vue";
import LabContactsDailog from "@/components/cycle-monitoring/lab-contacts-dialog.vue";
import { useToast } from "primevue/usetoast";

export default {
    components: {
        CycleMonitoringCycleNav,
        CycleMonitoringCycleInfo,
        CycleMonitoringDetailForm,
        CycleMonitoringDiagnosisForm,
        CycleMonitoringOtherDrugsForm,
        CycleMonitoringTherapyFrom,
        CycleMonitoringLineTable,
        CycleMonitoringComments,
        CycleMonitoringFeesForm,
        CycleMonitoringPlansForm,
        CycleMonitoringToolbar,
        CycleMonitoringLineToolbar,
        CycleMonitoringLineEditOrCreateDialog,
        CycleMonitoringStimulationForm,
        CycleMonitoringDeletedDayDialog,
        BornObstetricalHistoryForm,
        BornOvarianForm,
        BronExtraForm,
        OrderBloodTestConfirmDialog,
        OrderBloodTestDialog,
        LabContactsDailog
    },
    setup() {
        const toast = useToast();
        const store = useStore();
        const hasNoCycle = ref(false);
        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const { form, isValid } = useCycleMonitoringForm(store);
        provide("summaryForm", form);
        provide("isValid", isValid);

        const deletedDayDialog = ref(false);
        provide("deletedDayDialog", deletedDayDialog);

        const labContactsDailog = ref(false);
        provide("labContactsDailog", labContactsDailog);

        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );

        const onSave = () => {
            if (isValid.cycleInfoForm && isValid.otherDrugForm) {
                const formData = useFormatToMonitorSummaryReqBody({
                    data: { ...form },
                    store
                });

                if (
                    formData.cycleType !== "Investigative" &&
                    (!formData.diag1 || formData.diag1 === "") &&
                    (!formData.diag2 || formData.diag2 === "") &&
                    (!formData.diag3 || formData.diag3 === "") &&
                    (!formData.diag4 || formData.diag4 === "") &&
                    (!formData.diag5 || formData.diag5 === "")
                ) {
                    toast.add({
                        severity: "warn",
                        summary: "Diagnosis is required",
                        life: 3000
                    });

                    return;
                }

                store
                    .dispatch("cycleMonitoring/saveMonitoringSummary", formData)
                    .then((data) => {
                        toast.add({
                            severity: "success",
                            summary: "Save Successful",
                            life: 3000
                        });

                        // console.log(formData.cycleCancelCheck);

                        store.commit(
                            "artCycle/SET_SELECTED_CYCLE_CANCELLED",
                            formData.cycleCancelCheck
                        );
                        emitter.emit(
                            "cycle-monitoring:reload",
                            store.state.artCycle.selectedCycle.cycleId
                        );
                    });
            }
        };

        const getCycleMonitoringDetails = (cycleId) => {
            emitter.emit("app:show-page-loading", true);
            store
                .dispatch("cycleMonitoring/getCycleMonitoringDetails", cycleId)
                .then(() => {
                    emitter.emit("app:show-page-loading", false);
                })
                .catch(() => {});
        };

        const orderBloodTestDialog = ref(false);
        const orderBloodTestConfirmDialog = ref(false);
        const isCycle = ref(0);

        provide("orderBloodTestDialog", orderBloodTestDialog);
        provide("orderBloodTestConfirmDialog", orderBloodTestConfirmDialog);
        provide("isCycle", isCycle);

        // watch(selectedCycle, (newValue) => {
        //     getCycleMonitoringDetails(newValue.cycleId);
        // });

        onMounted(() => {
            if (store.state.artCycle.selectedCycle) {
                if (store.state.artCycle.selectedCycle.cycleId) {
                    hasNoCycle.value = false;
                    getCycleMonitoringDetails(
                        store.state.artCycle.selectedCycle.cycleId
                    );
                } else {
                    hasNoCycle.value = true;
                }
            }

            store.dispatch("cycleMonitoring/getFormFieldOptions");

            emitter.on("cycle-monitoring:reload", getCycleMonitoringDetails);
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring:reload", getCycleMonitoringDetails);
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { isBornCycle, hasNoCycle };
    }
};
</script>

<style>
#cycle-monitoriing .p-card .p-card-content {
    padding: 0 0;
}

#cycle-monitoriing .p-field {
    margin-bottom: 0.2rem;
}

#cycle-monitoriing .p-card {
    height: 100%;
}

#cycle-monitoriing .p-calendar,
.p-togglebutton {
    height: 24.5px;
}

#cycle-monitoriing .p-datatable.p-datatable-sm .p-datatable-tbody > tr > td {
    padding: 0.25rem;
}

#cycle-monitoriing .group-box .p-card-body {
    padding: 0.4rem;
}

#cycle-monitoriing .p-button {
    padding: 0.3rem 0.6rem;
}

#cycle-monitoriing .p-md-2,
.p-md-3,
.p-md-5,
.p-md-8 {
    padding-left: 1px;
    padding-right: 1px;
}
</style>
