<template>
    <p-card>
        <template #content>
            <div class="p-grid">
                <div class="p-col-6">
                    <div class="p-field p-grid">
                        <label for="diagnosis1">
                            Diagnosis
                        </label>
                    </div>

                    <div class="p-field p-grid">
                        <label class="p-col-fixed">1</label>
                        <p-dropdown
                            id="diagnosis1"
                            v-model="form.diagnosis1"
                            :options="diagnosisOptions"
                            optionLabel="label"
                            optionValue="value"
                            class="p-col"
                            style="max-width: 85%"
                            @change="onChange"
                            :showClear="true"
                        />
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed">2</label>
                        <p-dropdown
                            id="diagnosis2"
                            v-model="form.diagnosis2"
                            :options="diagnosisOptions"
                            optionLabel="label"
                            optionValue="value"
                            class="p-col"
                            style="max-width: 85%"
                            @change="onChange"
                            :showClear="true"
                        />
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed">3</label>
                        <p-dropdown
                            id="diagnosis3"
                            v-model="form.diagnosis3"
                            :options="diagnosisOptions"
                            optionLabel="label"
                            optionValue="value"
                            class="p-col"
                            style="max-width: 85%"
                            @change="onChange"
                            :showClear="true"
                        />
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed">4</label>
                        <p-dropdown
                            id="diagnosis4"
                            v-model="form.diagnosis4"
                            :options="diagnosisOptions"
                            optionLabel="label"
                            optionValue="value"
                            class="p-col"
                            style="max-width: 85%"
                            @change="onChange"
                            :showClear="true"
                        />
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed">5</label>
                        <p-dropdown
                            id="diagnosis5"
                            v-model="form.diagnosis5"
                            :options="diagnosisOptions"
                            optionLabel="label"
                            optionValue="value"
                            class="p-col"
                            style="max-width: 85%"
                            @change="onChange"
                            :showClear="true"
                        />
                    </div>
                </div>

                <div class="p-col-6">
                    <div class="p-field p-grid">
                        <span style="width: 100%; margin-bottom: 1px;"
                            >Issues</span
                        >
                    </div>
                    <div class="p-field p-grid">
                        <p-input-text
                            v-model="form.diagIssue1"
                            class="p-mt-1"
                            style="width: 100%; margin-bottom: 1px;"
                        />

                        <p-input-text
                            v-model="form.diagIssue2"
                            class="p-mt-1"
                            style="width: 100%; margin-bottom: 1px;"
                        />

                        <p-input-text
                            v-model="form.diagIssue3"
                            class="p-mt-1"
                            style="width: 100%;  margin-bottom: 1px;"
                        />

                        <p-input-text
                            v-model="form.diagIssue4"
                            class="p-mt-1"
                            style="width: 100%;  margin-bottom: 5px;"
                        />

                        <p-input-text
                            v-model="form.diagIssue5"
                            class="p-mt-1"
                            style="width: 100%"
                        />
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { reactive, computed, watch, onMounted, onUnmounted, inject } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const diagnosisOptions = computed(
            () => store.state.options.reasonsForTreatmentCycle
        );

        const summaryForm = inject("summaryForm");
        const form = reactive({
            diagnosis1: null,
            diagnosis2: null,
            diagnosis3: null,
            diagnosis4: null,
            diagnosis5: null,
            diagIssue1: null,
            diagIssue2: null,
            diagIssue3: null,
            diagIssue4: null,
            diagIssue5: null
        });
        watch(form, (newValue) => {
            summaryForm.diagnosis1 = newValue.diagnosis1;
            summaryForm.diagnosis2 = newValue.diagnosis2;
            summaryForm.diagnosis3 = newValue.diagnosis3;
            summaryForm.diagnosis4 = newValue.diagnosis4;
            summaryForm.diagnosis5 = newValue.diagnosis5;

            summaryForm.diagIssue1 = newValue.diagIssue1;
            summaryForm.diagIssue2 = newValue.diagIssue2;
            summaryForm.diagIssue3 = newValue.diagIssue3;
            summaryForm.diagIssue4 = newValue.diagIssue4;
            summaryForm.diagIssue5 = newValue.diagIssue5;
        });

        watch(monitorDetails, (newValue) => {
            form.diagnosis1 = newValue.diag1;
            form.diagnosis2 = newValue.diag2;
            form.diagnosis3 = newValue.diag3;
            form.diagnosis4 = newValue.diag4;
            form.diagnosis5 = newValue.diag5;

            form.diagIssue1 = newValue.diagIssue1;
            form.diagIssue2 = newValue.diagIssue2;
            form.diagIssue3 = newValue.diagIssue3;
            form.diagIssue4 = newValue.diagIssue4;
            form.diagIssue5 = newValue.diagIssue5;
        });

        const onSave = () => {
            // console.log("from diagnosis ", { ...form });
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return {
            diagnosisOptions,
            form,
            onChange: (event) => {
                emitter.emit("daignosis-form:on-diagnosis-select", event.value);
            }
        };
    }
};
</script>
