<template>
    <p-dialog
        header="Deleted Day"
        v-model:visible="deletedDayDialog"
        :style="{ width: '50vw' }"
        :modal="true"
        class="p-inputtext-sm"
    >
        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>From Date</label>
                <p-calendar
                    v-model="form.fromDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                    :class="{
                        'p-invalid': submitted && v$.fromDate.$invalid
                    }"
                />
            </div>
            <div class="p-field">
                <label>To Date</label>
                <p-calendar
                    v-model="form.toDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                    :class="{
                        'p-invalid': submitted && v$.toDate.$invalid
                    }"
                />
            </div>
            <div class="p-field">
                <label>Patient</label>
                <p-input-text v-model="form.patient" :showIcon="true" />
            </div>
            <p-button label="Filter" class="p-button-sm" @click="filter" />
        </div>

        <div style="height: 450px" class="p-mt-2">
            <p-data-table
                :value="deleteLog"
                :loading="loading"
                dataKey="cmLinesId"
                showGridlines
                class="p-datatable-sm"
                :scrollable="true"
                scrollHeight="flex"
                :paginator="true"
                :rows="10"
                paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
                :rowsPerPageOptions="[10, 20, 50]"
                responsiveLayout="scroll"
                currentPageReportTemplate="Showing {first} to {last} of {totalRecords}"
            >
                <p-column
                    header="Monitor Day"
                    field="cDate"
                    style="max-width: 100px"
                >
                </p-column>
                <p-column header="Comments" field="comment"> </p-column>
                <p-column
                    header="Patient ID"
                    field="patientId"
                    style="max-width: 100px"
                >
                </p-column>
                <p-column
                    header="Deleted By"
                    field="deleteBy"
                    style="max-width: 100px"
                >
                </p-column>
                <p-column
                    header="Deleted On"
                    field="deleteDate"
                    style="max-width: 100px"
                >
                </p-column>
                <p-column style="max-width: 80px">
                    <template #body="{data}">
                        <p-button
                            label="Restore"
                            class="p-button-sm"
                            @click="restore(data.cmLinesId)"
                        />
                    </template>
                </p-column>
            </p-data-table>
        </div>
    </p-dialog>
</template>

<script>
import { useDeletedDaysDialog } from "@/composable/cycle-monitoring";
export default {
    setup() {
        const {
            deletedDayDialog,
            form,
            v$,
            submitted,
            filter,
            deleteLog,
            loading,
            restore
        } = useDeletedDaysDialog();

        return {
            deletedDayDialog,
            form,
            v$,
            submitted,
            filter,
            deleteLog,
            loading,
            restore
        };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
</style>
