<template>
    <div style="padding-bottom: 20px;">
        <p-button
            label="Previous"
            icon="pi pi-chevron-left"
            class="p-mr-2 app-button p-button-sm"
            @click="previousCycle"
            :loading="prevBtnLoading"
        />
        <p-button
            label="Next"
            icon="pi pi-chevron-right"
            iconPos="right"
            class="app-button p-button-sm"
            @click="nextCycle"
            :loading="nextBtnLoading"
        />
    </div>
</template>

<script>
import { computed, ref } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const prevBtnLoading = ref(false);
        const nextBtnLoading = ref(false);

        const selectedMonitorLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );

        return {
            prevBtnLoading,
            nextBtnLoading,
            selectedMonitorLine,
            nextCycle: () => {
                // store.dispatch("artCycle/nextCycle");

                nextBtnLoading.value = true;
                store
                    .dispatch("artCycle/nextCycle")
                    .then(() => {
                        // zubaer commented out this for fixing the daouble api call
                        return store.dispatch(
                            "cycleMonitoring/getCycleMonitoringDetails",
                            store.state.artCycle.selectedCycle.cycleId
                        );
                    })
                    .finally(() => {
                        nextBtnLoading.value = false;
                    });
            },
            previousCycle: () => {
                // store.dispatch("artCycle/previsouCycle");

                prevBtnLoading.value = true;
                store
                    .dispatch("artCycle/previsouCycle")
                    .then(() => {
                        // zubaer commented out this for fixing the daouble api call
                        return store.dispatch(
                            "cycleMonitoring/getCycleMonitoringDetails",
                            store.state.artCycle.selectedCycle.cycleId
                        );
                    })
                    .finally(() => {
                        prevBtnLoading.value = false;
                    });
            }
        };
    }
};
</script>

<style scoped>
.p-toolbar {
    background-color: transparent;
    border: none;
}
</style>
