<template>
    <p-card>
        <template #content>
            <div class="p-grid">
                <div
                    class="p-col"
                    style="min-width: 590px; min-height: 160px; margin-right: 2px;"
                >
                    <div
                        style="display: flex; flex-direction: column; justify-content: flex-start;"
                    >
                        <div style="display: flex;" class="container1">
                            <div>
                                <label class="p-mr-2">Protocol</label>
                                <p-dropdown
                                    v-model="summaryForm.stimProt2"
                                    :options="protocolOpts"
                                    optionLabel="label"
                                    optionValue="value"
                                />
                            </div>
                            <div>
                                <label class="p-mx-2 p-mb-0">
                                    <span style="font-size: 14px">
                                        Luteal Support
                                    </span>
                                </label>
                                <p-checkbox
                                    v-model="summaryForm.letSupport"
                                    :binary="true"
                                />
                            </div>
                        </div>
                        <div style="display: flex;" class="container2">
                            <div>
                                <div
                                    class="p-field p-grid"
                                    style="width: 290px"
                                ></div>
                                <div class="p-field p-grid">
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.simDrug1"
                                            :options="stimulationDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed p-mt-1"
                                        style="width: 40px"
                                    >
                                        Dose
                                    </label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.simDose1"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="simDrugsVisible[1]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.simDrug2"
                                            :options="stimulationDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.simDose2"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="simDrugsVisible[2]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.simDrug3"
                                            :options="stimulationDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.simDose3"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="simDrugsVisible[3]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.simDrug4"
                                            :options="stimulationDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.simDose4"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>
                                <p-button
                                    v-if="simDrugBtnVisibilty"
                                    label="Add"
                                    @click="addSimDrugs"
                                    class="p-mb-2"
                                />
                            </div>
                            <div class="p-col-fixed">
                                <div class="p-field p-grid">
                                    <label class="p-col-fixed">Drug</label>
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.letDrug1"
                                            :options="lutealDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    >
                                        Dose
                                    </label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.letDose1"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="letDrugsVisible[1]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.letDrug2"
                                            :options="lutealDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.letDose2"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="letDrugsVisible[2]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.letDrug3"
                                            :options="lutealDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.letDose3"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <div
                                    class="p-field p-grid"
                                    v-if="letDrugsVisible[3]"
                                >
                                    <label class="p-col-fixed p-mt-1"
                                        >Drug</label
                                    >
                                    <div class="p-col">
                                        <p-dropdown
                                            v-model="summaryForm.letDrug4"
                                            :options="lutealDrugOpts"
                                            optionLabel="name"
                                            optionValue="name"
                                            style="width: 120px;"
                                        />
                                    </div>
                                    <label
                                        class="p-col-fixed"
                                        style="width: 40px"
                                    ></label>
                                    <div class="p-col">
                                        <p-input-text
                                            v-model="summaryForm.letDose4"
                                            style="width: 60px"
                                        />
                                    </div>
                                </div>

                                <p-button
                                    v-if="letDrugBtnVisibilty"
                                    label="Add"
                                    @click="addLetDrug"
                                    class="p-mb-2"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div class="p-col">
                    <div style="display: flex; justify-content: space-around;">
                        <div style="display: flex; flex-direction: column;">
                            <div class="p-mb-2">
                                <label class="">
                                    Trigger
                                </label>
                                <div class="">
                                    <p-dropdown
                                        v-model="summaryForm.trigger"
                                        optionLabel="label"
                                        optionValue="value"
                                        :options="triggerOptions"
                                    />
                                </div>
                            </div>
                            <div class="">
                                <label class="p-mb-0">
                                    Fert
                                </label>
                                <div class="">
                                    <p-dropdown
                                        v-model="summaryForm.fert"
                                        optionLabel="label"
                                        optionValue="value"
                                        :options="fertOptions"
                                    />
                                </div>
                            </div>
                        </div>
                        <div class="p-ml-2">
                            <label class="">Comment</label>
                            <div class="">
                                <p-textarea
                                    v-model="summaryForm.comments"
                                    style="width: 100%; height: 100px;"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="p-grid">
                <span>
                    {{ monitorDetails ? monitorDetails.stimProtocol : "" }}
                </span>
                {{ monitorDetails ? monitorDetails.stimProtocolText : "" }}
                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox v-model="summaryForm.hcgiu" :binary="true" />
                    <label for="city1">HCG Wash</label>
                </div>

                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox
                        v-model="summaryForm.intralipid"
                        :binary="true"
                    />
                    <label for="city2">Intralipid</label>
                </div>

                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox v-model="summaryForm.fragmin" :binary="true" />
                    <label for="city1">Fragmin</label>
                </div>

                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox v-model="summaryForm.neurogen" :binary="true" />
                    <label for="city2">Neupogen</label>
                </div>

                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox
                        v-model="summaryForm.embryoGlue"
                        :binary="true"
                    />
                    <label for="city2">Embryo Glue</label>
                </div>
                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox v-model="summaryForm.pgs" :binary="true" />
                    <label for="city3">PGT</label>
                </div>
                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox v-model="summaryForm.tese" :binary="true" />
                    <label for="city4">TESE</label>
                </div>
                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox
                        v-model="summaryForm.caIonaphore"
                        :binary="true"
                    />
                    <label for="city3">Ca lanophore</label>
                </div>
                <div class="p-field-checkbox p-mr-3">
                    <p-checkbox
                        v-model="summaryForm.imsi"
                        :binary="true"
                    />
                    <label for="city3">IMSI</label>
                </div>
                <div class="p-field-checkbox">
                    <p-checkbox
                        v-model="summaryForm.d_3"
                        :binary="true"
                    />
                    <label for="city3">D3</label>
                </div>
            </div>
        </template>
    </p-card>

    <medication-inventory-dialog />
</template>

<script>
import { TIME_0 } from "@/constants";
import { computed, inject, provide, ref, reactive, watch } from "vue";
import { useStore } from "vuex";
import { EMPTY_DRUG_OPTION } from "@/constants";

import medicationInventoryDialog from "./medication-inventory-dialog.vue";
export default {
    components: {
        medicationInventoryDialog
    },
    setup() {
        const store = useStore();
        const summaryForm = inject("summaryForm");

        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const letDrugs = ref(1);
        const simDrugs = ref(1);
        const letDrugsVisible = reactive([true, false, false, false]);
        const simDrugsVisible = reactive([true, false, false, false]);

        const triggerOptions = ref([
            { label: "HCG", value: "hcg" },
            { label: "Ovidrel", value: "ovidrel" },
            { label: "Lupron", value: "lupron" },
            { label: "Suprefact", value: "suprefact" }
        ]);

        const fertOptions = ref([
            { label: "ICSI", value: "icsi" },
            { label: "Split IVF/ICSI", value: "split_ivf_icsi" },
            { label: "IVF", value: "ivf" }
        ]);

        watch(monitorDetails, (newValue) => {
            summaryForm.letSupport = newValue.letSupport;

            summaryForm.letDose1 = newValue.letDose1;
            summaryForm.letDose2 = newValue.letDose2;
            summaryForm.letDose3 = newValue.letDose3;
            summaryForm.letDose4 = newValue.letDose4;

            summaryForm.letDrug1 = newValue.letDrug1;
            summaryForm.letDrug2 = newValue.letDrug2;
            summaryForm.letDrug3 = newValue.letDrug3;
            summaryForm.letDrug4 = newValue.letDrug4;

            summaryForm.simDose1 = newValue.simDose1;
            summaryForm.simDose2 = newValue.simDose2;
            summaryForm.simDose3 = newValue.simDose3;
            summaryForm.simDose4 = newValue.simDose4;

            summaryForm.simDrug1 = newValue.simDrug1;
            summaryForm.simDrug2 = newValue.simDrug2;
            summaryForm.simDrug3 = newValue.simDrug3;
            summaryForm.simDrug4 = newValue.simDrug4;

            summaryForm.stimProt2 = newValue.stimProtocol2;
            summaryForm.lmpDate = newValue.lmpDate
                ? new Date(newValue.lmpDate + TIME_0)
                : null;
            summaryForm.lengthInDays = newValue.lengthInDays;
            summaryForm.comments = newValue.comments;

            summaryForm.fert = newValue.fert;
            summaryForm.trigger = newValue.trigger;
            summaryForm.pgs = newValue.pgs;
            summaryForm.tese = newValue.tese;
            summaryForm.caIonaphore = newValue.caIonaphore;
            summaryForm.imsi = newValue.imsi;
            summaryForm.d_3 = newValue.d_3;          

            if (newValue.letDrug2 && newValue.letDrug2 != "") {
                letDrugsVisible[1] = true;
            } else {
                letDrugsVisible[1] = false;
            }

            if (newValue.letDrug3 && newValue.letDrug3 != "") {
                letDrugsVisible[2] = true;
            } else {
                letDrugsVisible[2] = false;
            }

            if (newValue.letDrug4 && newValue.letDrug4 != "") {
                letDrugsVisible[3] = true;
            } else {
                letDrugsVisible[3] = false;
            }

            if (newValue.simDrug2 && newValue.simDrug2 != "") {
                simDrugsVisible[1] = true;
            } else {
                simDrugsVisible[1] = false;
            }

            if (newValue.simDrug3 && newValue.simDrug3 != "") {
                simDrugsVisible[2] = true;
            } else {
                simDrugsVisible[2] = false;
            }

            if (newValue.simDrug4 && newValue.simDrug4 != "") {
                simDrugsVisible[3] = true;
            } else {
                simDrugsVisible[3] = false;
            }
        });

        watch(
            () => [
                summaryForm.simDrug1,
                summaryForm.simDrug2,
                summaryForm.simDrug3,
                summaryForm.simDrug4
            ],
            ([nd1, nd2, nd3, nd4]) => {
                if (nd1 == null) {
                    summaryForm.simDose1 = null;
                }
                if (nd2 == null) {
                    summaryForm.simDose2 = null;
                }
                if (nd3 == null) {
                    summaryForm.simDose3 = null;
                }
                if (nd4 == null) {
                    summaryForm.simDose4 = null;
                }
            }
        );

        watch(
            () => [
                summaryForm.letDrug1,
                summaryForm.letDrug2,
                summaryForm.letDrug3,
                summaryForm.letDrug4
            ],
            ([nd1, nd2, nd3, nd4]) => {
                if (nd1 == null) {
                    summaryForm.letDrug1 = null;
                }
                if (nd2 == null) {
                    summaryForm.letDrug2 = null;
                }
                if (nd3 == null) {
                    summaryForm.letDrug3 = null;
                }
                if (nd4 == null) {
                    summaryForm.letDrug4 = null;
                }
            }
        );

        const medicationInventoryDialog = ref(false);
        provide("medicationInventoryDialog", medicationInventoryDialog);

        const stimulationDrugOpts = computed(() => [
            ...store.state.cycleMonitoring.inventoryList.filter(
                (item) => item.code === "Stimulation"
            ),
            { name: EMPTY_DRUG_OPTION }
        ]);

        const lutealDrugOpts = computed(() => [
            ...store.state.cycleMonitoring.inventoryList.filter(
                (item) => item.code === "Luteal"
            ),
            { name: EMPTY_DRUG_OPTION }
        ]);

        const protocolOpts = computed(() => store.state.options.monProtocols);

        const letDrugBtnVisibilty = computed(
            () => letDrugsVisible.filter((item) => item).length < 4
        );
        const simDrugBtnVisibilty = computed(
            () => simDrugsVisible.filter((item) => item).length < 4
        );

        return {
            monitorDetails,
            summaryForm,
            stimulationDrugOpts,
            lutealDrugOpts,
            protocolOpts,
            showMedicationInventoryDialog: () => {
                medicationInventoryDialog.value = true;
            },
            letDrugs,
            simDrugs,
            addLetDrug: () => {
                let items = letDrugsVisible.filter((item) => item);
                letDrugsVisible[items.length] = true;
            },
            addSimDrugs: () => {
                let items = simDrugsVisible.filter((item) => item);
                simDrugsVisible[items.length] = true;
            },
            letDrugsVisible,
            simDrugsVisible,
            letDrugBtnVisibilty,
            simDrugBtnVisibilty,
            triggerOptions,
            fertOptions
        };
    }
};
</script>
<style scoped>
@media (max-width: 768px) {
    .container2 {
        display: flex;
        flex-direction: column;
    }
}
</style>
