<template>
    <p-dialog
        :header="dialogHeader"
        v-model:visible="visible"
        :style="{ width: '50vw' }"
        :modal="true"
        id="day-dialog"
        class="p-inputtext-sm"
        @show="fillForm"
    >
        <div class="p-fluid p-grid">
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Date
                    </label>
                    <div class="p-col">
                        <p-calendar
                            v-model="form.date"
                            dateFormat="yy-mm-dd"
                            :showIcon="true"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Cycle
                    </label>
                    <div class="p-col">
                        <strong>{{ form.cycleId }}</strong>
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Cycle Day
                    </label>
                    <div class="p-col">
                        <p-input-number
                            v-model="form.cycleDay"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Injection Day
                    </label>
                    <div class="p-col">
                        <p-input-number
                            v-model="form.injecttionDay"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Right Ovary Total
                    </label>
                    <div class="p-col">
                        <p-input-number
                            v-model="form.rightOvaryTotal"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Right OvaryCyst
                    </label>
                    <div class="p-col">
                        <input
                            v-model="form.rightOvaryCyst"
                            class="p-inputtext p-component"
                            style="width: 50px"
                            maxlength="25"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Left Ovary Total
                    </label>
                    <div class="p-col">
                        <p-input-number
                            v-model="form.leftOvaryTotal"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Left OvaryCyst
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.leftOvaryCyst"
                            class="p-inputtext p-component"
                            style="width: 50px"
                            maxlength="25"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        ET
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.et"
                            :max="9999.99"
                            type="number"
                            class="p-inputtext p-component"
                            style="width: 100px"
                            maxlength="10"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Polyp
                    </label>
                    <div class="p-col">
                        <input
                            v-model="form.polyp"
                            class="p-inputtext p-component"
                            style="width: 50px"
                            maxlength="5"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        GnT
                    </label>
                    <div class="p-col">
                        <input
                            v-model="form.gnt"
                            class="p-inputtext p-component"
                            style="width: 50px"
                            maxlength="10"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Drug 1
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.drug1"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Drug 2
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.drug2"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Drug 3
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.drug3"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-12 p-md-6">
                <div class="p-field p-grid">
                    <label class="p-col p-mb-0">
                        Drug 4
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.drug4"
                            style="width: 50px"
                        />
                    </div>
                </div>
            </div>
            <div class="p-col-fixed">
                <p-button label="Link With Blood Test" @click="linkBloodTest" />
            </div>
        </div>

        <p-data-table
            :value="ovaryFollicles"
            showGridlines
            rowGroupMode="subheader"
            groupRowsBy="type"
            class="p-datatable-sm p-mt-1 "
        >
            <template #groupheader="{data}">
                <span>{{ data.type }}</span>
            </template>
            <p-column field="type"> </p-column>
            <p-column field="input10l">
                <template #body="{data}">
                    <label class="p-text-bold">&#60;10</label>
                    <br />
                    <p-input-number v-model="data.input10l" />
                </template>
            </p-column>
            <p-column field="input11">
                <template #body="{data}">
                    <label class="p-text-bold">11</label>
                    <br />
                    <p-input-number v-model="data.input11" />
                </template>
            </p-column>
            <p-column field="input12">
                <template #body="{data}">
                    <label class="p-text-bold">12</label>
                    <br />
                    <p-input-number v-model="data.input12" />
                </template>
            </p-column>
            <p-column field="input13">
                <template #body="{data}">
                    <label class="p-text-bold">13</label>
                    <br />
                    <p-input-number v-model="data.input13" />
                </template>
            </p-column>
            <p-column field="input14">
                <template #body="{data}">
                    <label class="p-text-bold">14</label>
                    <br />
                    <p-input-number v-model="data.input14" />
                </template>
            </p-column>
            <p-column field="input15">
                <template #body="{data}">
                    <label class="p-text-bold">15</label>
                    <br />
                    <p-input-number v-model="data.input15" />
                </template>
            </p-column>
            <p-column field="input16">
                <template #body="{data}">
                    <label class="p-text-bold">16</label>
                    <br />
                    <p-input-number v-model="data.input16" />
                </template>
            </p-column>
            <p-column field="input17">
                <template #body="{data}">
                    <label class="p-text-bold">17</label>
                    <br />
                    <p-input-number v-model="data.input17" />
                </template>
            </p-column>
            <p-column field="input18">
                <template #body="{data}">
                    <label class="p-text-bold">18</label>
                    <br />
                    <p-input-number v-model="data.input18" />
                </template>
            </p-column>
            <p-column field="input19">
                <template #body="{data}">
                    <label class="p-text-bold">19</label>
                    <br />
                    <p-input-number v-model="data.input19" />
                </template>
            </p-column>
            <p-column field="input20">
                <template #body="{data}">
                    <label class="p-text-bold">20</label>
                    <br />
                    <p-input-number v-model="data.input20" />
                </template>
            </p-column>
            <p-column field="input21">
                <template #body="{data}">
                    <label class="p-text-bold">21</label>
                    <br />
                    <p-input-number v-model="data.input21" />
                </template>
            </p-column>
            <p-column field="input22">
                <template #body="{data}">
                    <label class="p-text-bold">22</label>
                    <br />
                    <p-input-number v-model="data.input22" />
                </template>
            </p-column>
            <p-column field="input23">
                <template #body="{data}">
                    <label class="p-text-bold">23</label>
                    <br />
                    <p-input-number v-model="data.input23" />
                </template>
            </p-column>
            <p-column field="input24">
                <template #body="{data}">
                    <label class="p-text-bold">24&#62;</label>
                    <br />
                    <p-input-number v-model="data.input24g" />
                </template>
            </p-column>
        </p-data-table>

        <div class="p-fluid" v-if="type === 'new'">
            <div class="p-field p-mt-1">
                <label>Comments</label>
                <p-textarea v-model="form.comments" />
            </div>
        </div>

        <p-divider />
        <p-button
            label="Save"
            icon="pi pi-check"
            class="p-mr-2"
            autofocus
            @click="save"
            :loading="loading"
        />
        <p-button
            label="Cancel"
            icon="pi pi-times"
            class="p-button-outlined p-button-danger"
            @click="close"
        />

        <p-dialog
            header="Edit Day"
            v-model:visible="errorDialog"
            :style="{ width: '200px' }"
            :modal="true"
        >
            <h3>{{ errorMsg }}</h3>
            <p-button label="Close" @click="closeErrorDialog" />
        </p-dialog>
    </p-dialog>
</template>

<script>
import { computed, onMounted, onUnmounted, reactive, ref } from "vue";
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import {
    useOvaryFollicles,
    useFormatToFromData,
    useFillForm
} from "@/composable/cycle-monitoring";
import { CYCLE_MONITOR_LINE_DAILOG_TYPE } from "@/constants";
import format from "date-fns/esm/format";
import { DATE_FORMAT_STRING } from "@/config";
import { linkWithBloodTest } from "@/service/cycle-monitoring";
import { useConfirm } from "primevue/useconfirm";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const store = useStore();
        const selectedLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );
        const visible = ref(false);
        const type = ref("edit");
        const ovaryFollicles = useOvaryFollicles();
        const errorDialog = ref(false);
        const errorMsg = ref("Date already Exits");
        const loading = ref(false);

        const form = reactive({
            cmLineId: null,
            date: null,
            cycleId: null,
            monitorId: null,
            cycleDay: null,
            injecttionDay: null,
            rightOvaryTotal: null,
            rightOvaryCyst: null,
            leftOvaryTotal: null,
            leftOvaryCyst: null,
            et: null,
            polyp: null,
            gnt: null,
            comments: null,
            drug1: null,
            drug2: null,
            drug3: null,
            drug4: null
        });

        const dialogHeader = computed(() => {
            if (form.cycleId && type.value === "edit") {
                return `Edit Day`;
            } else if (form.cycleId && type.value === "new") {
                return `New Day`;
            } else if (form.cycleId && type.value === "copy") {
                return `Copy Day`;
            } else {
                return "";
            }
        });

        const validate = () => {
            const dates = store.state.cycleMonitoring.lines.map((item) =>
                format(new Date(item.cycleDate), DATE_FORMAT_STRING)
            );
            const isIncludes = dates.includes(
                format(form.date, DATE_FORMAT_STRING)
            );
            return isIncludes;
        };

        const save = () => {
            let formData = useFormatToFromData(
                store.state.cycleMonitoring.details,
                { ...form },
                ovaryFollicles[1],
                ovaryFollicles[0]
            );

            if (
                type.value === CYCLE_MONITOR_LINE_DAILOG_TYPE.NEW ||
                type.value === CYCLE_MONITOR_LINE_DAILOG_TYPE.COPY
            ) {
                if (validate()) {
                    errorMsg.value = "Date already Exits";
                    errorDialog.value = true;
                    return;
                }
                loading.value = true;
                store
                    .dispatch("cycleMonitoring/createMonitorLine", formData)
                    .then(() => {
                        emitter.emit(
                            "cycle-monitoring:reload",
                            store.state.artCycle.selectedCycle.cycleId
                        );
                        visible.value = false;
                        loading.value = false;
                    })
                    .catch((err) => {
                        errorMsg.value = err.error;
                        errorDialog.value = true;
                        loading.value = false;
                    });
            }
            if (type.value === CYCLE_MONITOR_LINE_DAILOG_TYPE.EDIT) {
                loading.value = true;
                store
                    .dispatch("cycleMonitoring/editMonitorLine", {
                        ...formData,
                        monId: form.monitorId,
                        nlmLineId: form.cmLineId
                    })
                    .then(() => {
                        emitter.emit(
                            "cycle-monitoring:reload",
                            store.state.artCycle.selectedCycle.cycleId
                        );
                        visible.value = false;
                        loading.value = false;
                    })
                    .catch((err) => {
                        errorMsg.value = err.error;
                        errorDialog.value = true;
                        loading.value = false;
                    });
            }
        };

        const showDialog = (_type) => {
            visible.value = true;
            type.value = _type;
        };

        const confirm = useConfirm();
        const toast = useToast();

        const linkBloodTest = async () => {
            const resp = await linkWithBloodTest(
                selectedLine.value.cm_lines_id
            );

            let header;

            if (resp.status === "test_assinged") {
                header = "Success";
            } else if (resp.status === "already_attaced") {
                header = "Already Attched";
            } else {
                header = "Error Occred";
            }

            if (header === "Success" && resp.message !== "") {
                toast.add({
                    severity: "success",
                    summary: "Successful",
                    detail: resp.message,
                    life: 3000
                });

                emitter.emit(
                    "cycle-monitoring:reload",
                    store.state.artCycle.selectedCycle.cycleId
                );

                visible.value = false;
                loading.value = false;
            } else {
                toast.add({
                    severity: "error",
                    summary: "Not Found",
                    detail: "No blood result found for that day",
                    life: 3000
                });
            }
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-cycle-nav:edit-day", showDialog);
            emitter.on("cycle-monitoring-day-toolbar:new-day", showDialog);
            emitter.on("cycle-monitoring-day-toolbar:copy-day", showDialog);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-cycle-nav:edit-day", showDialog);
            emitter.off("cycle-monitoring-day-toolbar:new-day", showDialog);
            emitter.off("cycle-monitoring-day-toolbar:copy-day", showDialog);
        });

        const fillForm = useFillForm({
            type,
            form,
            selectedLine,
            ovaryFollicles,
            store
        });

        return {
            dialogHeader,
            visible,
            ovaryFollicles,
            type,
            form,
            save,
            fillForm,
            errorDialog,
            errorMsg,
            loading,
            linkBloodTest,
            close: () => (visible.value = false),
            closeErrorDialog: () => (errorDialog.value = false)
        };
    }
};
</script>

<style>
#day-dialog .p-inputnumber-input {
    width: 100%;
}
#day-dialog .p-datatable-thead {
    display: none;
}
</style>
