            form.cycleDay = null;
            form.injecttionDay = null;
            form.rightOvaryTotal = null;
            form.rightOvaryCyst = null;
            form.leftOvaryTotal = null;
            form.leftOvaryCyst = null;
            form.et = null;
            form.polyp = null;
            form.gnt = null;
