export function useDeletedDaysDialog() {
    const store = useStore();
    const deletedDayDialog = inject("deletedDayDialog");

    const form = reactive({
        fromDate: null,
        toDate: null,
        patient: null
    });

    const rules = {
        fromDate: { required },
        toDate: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const deleteLog = ref();
    const loading = ref(false);

    const filter = () => {
        submitted.value = true;
        if (v$.value.$invalid) return;

        loading.value = true;
        store
            .dispatch("cycleMonitoring/getMonitorLineDeleteLog", {
                fromDate: format(form.fromDate, DATE_FORMAT_STRING),
                toDate: format(form.toDate, DATE_FORMAT_STRING),
                patientId: form.patient
            })
            .then((data) => {
                deleteLog.value = data;
            })
            .finally(() => {
                loading.value = false;
            });
    };

    const restore = (cmLineId) => {
        loading.value = true;
        store
            .dispatch("cycleMonitoring/restoreMonitorLine", cmLineId)
            .then((resp) => {
                filter();
            });
    };

    return {
        deletedDayDialog,
        form,
        v$,
        submitted,
        filter,
        deleteLog,
        loading,
        restore
    };
}
